import React from "react";
import axios from "axios";
import { render } from "@testing-library/react";

export default class Watchlist extends React.Component {
  state = {
    info: [],
    name: "",
    names: [],
    posters: [],
  };
  componentDidMount() {
    let unStore = JSON.parse(localStorage.getItem("items"));
    console.log(unStore);
    // this.setState({ info: unStore });
    console.log(unStore[0]);
    this.setState({ name: unStore[0] });
    console.log(this.state.name);
    console.log(unStore.length);

    this.search(unStore);
    console.log(this.state.info);

    this.setState({ info: unStore });
  }

  async search(unStore) {
    var key = "3e5977f9";
    var URLs = [];
    const data = [];
    const images = [];
    const titles = [];
    for (let i = 0; i < unStore.length; i++) {
      URLs[i] = `https://www.omdbapi.com/?t=${unStore[i]}&apikey=${key}`;
    }

    for (let i = 0; i < unStore.length; i++) {
      axios.get(URLs[i]).then((res) => {
        data[i] = res.data;
        images[i] = data[i].Poster;
        titles[i] = data[i].Title;
        // console.log(image);
        console.log(data);
        console.log(titles[0]);
        console.log(titles);
        this.setState({ names: titles, posters: images });
        console.log(this.state.names);
      });

      /* const listItemsIMG = images.map((image, i) => {
        <img key={i} src={image} alt="poster" width="150" height="200" />;
      });
      console.log(titles);
      const listItemsTitle = titles.map((title, i) => {
        <p key={i}>{title}</p>;
      });*/

      this.setState({
        info: [<ul>t</ul>],
      });
    }
  }

  parseNamesAndPosters = (names, posters) => {
    const listItems = names.map((name, i) => (
      <li key={i}>
        {name}
        <img src={posters[i]} alt="poster" width="150" height="200" />
      </li>
    ));
    console.log(names);
    return <ul>{listItems}</ul>;
  };

  render() {
    return (
      <div>
        <header>Watchlist</header>
        {this.state.info}
      </div>
    );
  }
}
